    DynamicJkmcRangeParams,
    DynamicCkmcRangeParams,
    DynamicJxbyxjsgNumberParams,
    DynamicYxbjxjsgNumberParams,
    DynamicHxgzmParams,
    DynamicJklwsSelectParams,
    DynamicYxjkdParams,
    DynamicYxjsdParams,
    DynamicDmtkParams,
    DynamicDmtsParams,
    DynamicPbmParams,
    DynamicCsklrskNumberParams,
    DynamicCsklrmqParams,
    DynamicJkdParams,
    DynamicJkjbdParams,
    DynamicCkjbdParams,
    DynamicDsdParams
  }
})
export default class Chapter4Section213 extends Vue {
  public title = '4.2.1 ';
  public explainText = '';

  public initData = {
    A_k: '54.4',
    n_z: '0.014'
  };

    public data = {
      A_k: '',
      n_z: ''

    };

  public formulas = {
    0: '$$\\mu :$$',
    1: '$$A_k :,m^2$$',
    2: '$$\\zeta :$$',
    3: '$$A_i :\\zeta_i$$',
    4: '$$l_i :i$$',
    5: '$$\\phi :,0.97$$'
  };

  public demoContent =
    ',1860.4m,,1795.0m,2-4.8 x 8.0m,,2 1.2m ,2 0.5m 13.5m20 m12.4 x 8m 9m9m 331.2m ,i=0.00453,,R=50m,=22,34.2m,163.73m,133.27m  22 m  9m  8 x 6.8m  27 .20m 1793.5m , 3m ,, 8 x 6.8m ';
  public demoResult = '0.77';

  // 
  private paramJSKData = [];
  private paramJKLWSData =[];

  private paramJKMCData = [];
  private paramCKMCData =[];

  private paramJXBYXJSGData = [];
  private paramYXBJXJSGData = [];
  private paramHXGZMData = [];

  private paramYXHWGData = [];
  private paramYXJWGData = [];

  private paramCSKLRSKData=[];
  private paramCSKLRMQData=[];

  private paramYXJKDData = [];
  private paramYXJSDData=[];

  private paramDMTKData =[];
  private paramDMTSData =[];

  private paramPBMData=[];

  // 
