  public beforeOnDemo() {
    this.beforeOnReset()

    let jsk = this.$refs.jsk as any
    jsk.addFieldWithData(0.1, 4.8, 8, 0, 2, crossSectionShapeType.RECTANGLE)
    jsk.onParamsDataChange()

    let jkmc = this.$refs.jkmc as any
